div.mainpage{
    header{
        position: fixed;
        width: 100%;
        background-color: #3a3f48;
        height: 70px;
        z-index: 1000;
        transition: 0.3s height;
        &.open{
            height: 100vh;
            @media screen and (min-width: 850px) {
                height: 70px;
            }
        }
        .elementscontainer{
            height: 70px;
            padding: 10px 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .right{
                padding: 0 10px;
                width: 50%;
                height: 100%;
                display: flex;
                align-items: center;
                .menu{
                    display: none;
                    margin: 0 10px;
                    @media screen and (max-width: 850px) {
                        display: inline-block;
                    }
                    .nav-icon {
                        width: 40px;
                        height: 25px;
                        position: relative;
                        margin: 10px;
                        transform: rotate(0deg);
                        transition: .5s ease-in-out;
                        cursor: pointer;
                        span {
                            display: block;
                            position: absolute;
                            height: 5px;
                            width: 100%;
                            background: white;
                            border-radius: 9px;
                            opacity: 1;
                            left: 0;
                            transform: rotate(0deg);
                            transition: .25s ease-in-out;

                            &:nth-child(1) {
                                top: 0px;
                            }

                            &:nth-child(2), &:nth-child(3) {
                                top: 10px;
                            }

                            &:nth-child(4) {
                                top: 20px;
                            }
                        }

                        &.open {

                            span:nth-child(1) {
                                top: 18px;
                                width: 0%;
                                left: 50%;
                            }

                            span:nth-child(2) {
                                -webkit-transform: rotate(45deg);
                                -moz-transform: rotate(45deg);
                                -o-transform: rotate(45deg);
                                transform: rotate(45deg);
                            }

                            span:nth-child(3) {
                                -webkit-transform: rotate(-45deg);
                                -moz-transform: rotate(-45deg);
                                -o-transform: rotate(-45deg);
                                transform: rotate(-45deg);
                            }

                            span:nth-child(4) {
                                top: 18px;
                                width: 0%;
                                left: 50%;
                            }
                        }
                    }

                }
                .headercontent{
                    margin: 0 10px;
                    min-width: 180px;
                    height: 45px;
                    display: flex;
                    align-items: center;
                    @media screen and (max-width: 850px) {
                        display: none;
                    }
                    .pic{
                        width: 30%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background-color: #1d8386;
                        border-bottom-right-radius: 5px;
                        border-top-right-radius: 5px;
                        img{
                            height: 80%;
                        }
                    }
                    .content{
                        width: 70%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background-color: #32333a;
                        border-bottom-left-radius: 5px;
                        border-top-left-radius: 5px;
                        p{
                            color: white;
                            font-size: 0.8em;
                            font-weight: bold;
                            font-family: jf;
                        }
                    }
                }
            }

            .left{
                padding: 0 10px;
                width: 50%;
                height: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                .socitem{
                    min-width: 50px;
                    width: 50px;
                    min-height: 50px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: 0 15px;
                    div.soc-img{
                        background-repeat: no-repeat;
                        background-position: center center;
                        width: 100%;
                        height: 100%;
                        border: 3px solid white;
                        border-radius: 50%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        &.google-plus{
                            background-size: auto 50%;
                            background-image: url('/dist/images/google.svg');
                        }
                        &.twitter{
                            background-size: auto 60%;
                            background-image: url('/dist/images/twitter.svg');
                        }
                        &.facebook{
                            background-size: auto 70%;
                            background-image: url('/dist/images/facebook.svg');
                        }
                    }
                }
            }
            &.mobile{
                display: none;
                @media screen and (max-width: 850px) {
                    display: block;
                }
            }
        }
    }







