                border-top-left-radius: 20px;
                border-top-right-radius: 20px;
                display: inline-block;
                color: white;
                font-family: jf;
                font-size: 1em;
            }
            .border-topic{
                padding: 10px;
                width: 100%;
                border: 3px solid #3a3f48;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-wrap: wrap;
                border-top-left-radius: 10px;
                border-bottom-right-radius: 10px;
                border-bottom-left-radius: 10px;
                a{
                    display: inline-block;
                    min-width: 120px;
                    width: 15.4%;
                    height: 150px;
                    margin: 5px;
                    position: relative;
                    border-radius: 5px;
                    overflow: hidden;
                    .topic-img{
                        width: 100%;
                        height: 100%;
                        background-size: cover;
                        background-repeat: no-repeat;
                    }
                    p{
                        position: absolute;
                        bottom: 0;
                        width: 100%;
                        background-color: rgba(58, 63, 72, 0.8);
                        font-family: jf;
                        font-size: 0.8em;
                        padding: 5px 2px;
                        color: white;
                        text-align: center;
                    }
                }
            }
        }

        .download_desc{
            padding: 25px;
        }
        .downloadlinks{
            padding: 20px 10px 25px;
            width: 80%;
            min-width: 256px;
            text-align: center;
            color: white;
            font-family: jf;
            .link{
                position: relative;
                margin: 60px 0 20px;
            }
            h3{
                z-index: 1;
                top: -40px;
                margin: 0;
                left: 50%;
                transform: translateX(-50%);
                position: absolute;
                background-color: #7b7979;
                display: inline-block;
                font-size: 1em;
                padding: 10px 60px;
                border-radius: 15px;
                min-width: 200px;
                height: 100px;
                @media screen and (max-width: 850px) {
                    min-width: auto;
                    padding: 5px 20px 10px;
                    transform: translateX(0);
                    position: absolute;
                    top: -35px;
                    height: auto;
                    right: 40px;
                    left: auto;
                    border-radius: 10px;
                }
            }
            ul{
                z-index: 2;
                position: relative;
                border-radius: 15px;
                margin: 0;
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                background-color: #adadad;
                padding: 12px 0;
                border: 2px solid #7b7979;
                li{
                    min-width: 178px;
                    width: 15%;
                    width: 15%;
                    border-radius: 5px;
                    font-size: 0.8em;
                    margin: 5px;
                    padding: 5px;
                    list-style: none;
                    background-color: #3a3f48;
                    a{
                        color: white;
                        &:hover, &:visited {
                            color: white;
                        }
                        display: inline-block;
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}